<script>
	import { grabable } from '$lib/directives/grabable';
	import DemoDelegated from '$lib/components/DemoDelegated.svelte';

	export let node;
	export let connectable;

	function handleMove(e) {
		node.x = e.detail.x;
		node.y = e.detail.y;
	}
</script>

<div
	class="box"
	style="position: absolute; top:{node.y}px; left: {node.x}px; user-select: none;"
	use:grabable={{ nodeData: node }}
	use:connectable={{
		startPoint: { component: DemoDelegated, show: true },
		source: { glue: false }
	}}
	on:move={handleMove}
	on:end={handleMove}
>
	{node?.x}, {node?.y}
	<slot />
</div>

<style>
	div.box {
		width: 200px;
		height: 200px;
		margin: 1em;
		padding: 1em;
		border-radius: 1em;
		color: black;
		border: 1px solid rgba(255, 0, 0, 0.8);
	}
</style>
